/* Private *****************************************************************/

static double move_func()
{
    // TODO
    return ((double) rand()) / RAND_MAX;
}

static VertexPointer pick_candidate_random(const VertexSet vs)
{
    int rvi;
    rvi = ((double) (rand()) / RAND_MAX) * vs.n;
    assert(rvi <= vs.n);
    return VertexSet_get_vertex(vs, rvi);
}

static VertexPointer pick_candidate_high_energy(const VertexSet vs)
{
    VertexPointer chosen = *vs.set;
    int i;
    for (i = 0; i < vs.n; i++) {
         
    }
    return chosen;
}

static Vertex mutate(const GraphPointer graph)
{
    assert(graph);
    assert(graph->vs.n);
    
    VertexSet vs = graph->vs;
    
    VertexPointer chosen = pick_candidate_high_energy(vs); 

    Vertex old_v;
    old_v = Vertex_copy(*chosen);

    double rx, ry;
    rx = move_func();
    ry = move_func();
    
    Vertex_move(chosen, Vector_initialize(rx, ry));

    return old_v;
}

/* Public ******************************************************************/

void GlobalMinimizer_run(
        const GraphPointer graph,
        void (*e_fun)(GraphPointer)
    )
{
    assert(graph);
    
    srand(time(NULL));

    int i; 
    for (i = 0; i < G_ITMAX; i++) {
        e_fun(graph);

        double old_e; 
        old_e = graph->energy;

        Vertex old_v;
        old_v = mutate(graph);

        e_fun(graph);
        double new_e; 
        new_e = graph->energy;

        if (old_e < new_e) {
            VertexPointer new_v;
            new_v  = *(graph->vs.set + old_v.id);
            *new_v = old_v;
        }
    }
}

